import {request} from '../../utils/request.js'
export default {
	data(){
		return {
			categoryDatas: [],
			navIndex: 0
		}
	},
	async mounted(){
		let categoryDatas = await request('/getCategoryDatas')
		// 更新状态categoryDatas的状态值
		this.categoryDatas = categoryDatas
	},
	methods: {
		changeActive(index){
			this.navIndex = index
		},
		toList(){
			wx.navigateTo({
				url: '/pages/list/list'
			})
		}
	}
}
